.cFooterWrapper{
    width: 100%;
    margin-top: -2rem;
    background-color: #F8F8F8;
    height: 50vh;
}

.cFooterWrapper>hr{
    width: 100%;
    border: none;
    height: 1px;
    background: white;
    margin-top: 1rem;
}

.cFooter{
    display: flex;
    justify-content: space-around;
    width: 60%;
    color: black;
    margin-left: 18rem;
    gap: 8rem;
    margin-top: 5rem;
}

.logo{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: -2rem;
    justify-content: flex-end;
    align-items: flex-start;
}

.logo>img{
    width: 6rem;
    height: 2.5rem;
    margin-top: -0.5rem;
}

.logo>span{
    font-weight: 600;
    font-size: 1rem;
}

.block{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    max-width: 12rem;
}

.detail{
    display: flex;
    flex-direction: column;
    width: inherit;
    font-size: .7rem;
    gap: 1rem;
}

.detail>span:nth-of-type(1){
    font-weight: 600;
    font-style: normal;
    font-size: 0.7rem;
}

.pngLine:hover{
    cursor: pointer;
}

.pngLine{
    display: flex;
    gap: 1rem;
    align-items: center;
    color:#4F5665
}

.icon{
    width: 25px;
}

.logotext {
    gap: 1rem;
    align-items: center;
    color:#4F5665;
    width: 10rem;
    font-size: .7rem;
    margin-top: 1rem;
    margin-left: 1rem;
}

.copyRight {
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: center;
    font-size: .65rem;
    margin-top: 0.1rem;
    margin-left: -2rem;
}

.social {
    display: flex;
    flex-direction: row;
    gap: 1.6rem;
    margin-left: -1rem;
}

.socialimg {
    width: 3.5rem;
    height: 3.5rem;
    margin-left: 1rem;
    margin-top: 1rem;
    cursor: pointer;
}


@media screen and (max-width: 768px) {
    .cFooter{
        justify-content: flex-start;
        flex-wrap: wrap;
        align-items: flex-start;
        font-size: 1.7rem;
        gap: 2rem;
    }
}


@media screen and (max-width: 640px) {
    .cFooter {
        justify-content: center;
        /* flex-wrap: wrap;
        align-items: flex-start;
        font-size: .7rem; */
    }

    .logo{
        display: none;
    }
}