.plan{

background-color: #F6F6F6;
justify-content: center;
align-items: center;
height: 210vh;
}

.planTop {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    margin-top: 2rem;
}

.freeText {
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 2rem;
}

.h21{
    margin-top: 4rem;
    color: black;
    font-weight: 500;
}

.freeCover {
    display: flex;
    flex-direction: row;
    gap: 3rem;
}

.h2{
    color: black;
    margin-left: 3rem;
    margin-top: 3rem;
    margin-bottom: 0.4rem;
    font-size: small;
}

.freeImg {
    height: 6rem;
    width: 6rem;
    margin-top: 3.5rem;
    margin-left: 2rem;
}

.free {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.h3 {
    margin-left: 2rem;
    font-size: small;
}
.freeContainer {
    background-color: white;
    height: 37rem;
    width: 13rem;
    border-radius: 0.5rem;
    justify-content: center;
    align-items: center;
    padding-left: 4rem;
}

.good {
    height: 0.8rem;
    width: 0.8rem;
    list-style: none;
    margin-right: 1rem;

}

.fList {
    list-style: none;
    gap: 1rem;
    display: flex;
    flex-direction: column;
    margin-left: -3rem;
    color: #4F5665;
    font-size: x-small;
}

.button {
    color: black;
    background-color: white;
    border: none;
    border-radius: 0.8rem;
    width: 6rem;
    height: 1.5rem;
    margin-top: -1.5rem;
    margin-left: 1.5rem;
    border: 1px solid #F53838;
}

.button:hover {
    background-color: #F53838;
    color: white;
}

.globe {
    margin-left: 25rem;
    margin-top: -5rem;
    padding: 0.1rem;
}

.sponsored {
    margin-top: -18rem;
    margin-left: 25rem;
    margin-bottom: -14rem;
}

.imgs {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}